.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 300px;
  width: 100%;
  background-color: #87c0c0;
  min-height: 150px;
  border-radius: 10px;
  margin: 5px;

  &__welcome {
    text-transform: uppercase;
    margin-top: 20px;
  }
}
